import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import footerImg from "../../images/footerlogo.png"
import "./Footer.css"
import { Link } from "gatsby"
import Loader from "../Loader/Loader"
// reactstrap components
import {
  Button,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col,
  UncontrolledTooltip,
} from "reactstrap"

export default function Footer() {
  return (
    <div>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        className="waves"
        preserveAspectRatio="none"
        viewBox="0 24 150 28"
      >
        <defs>
          <path
            id="gentle-wave"
            d="M-160 44c30 0 58-18 88-18s58 18 88 18 58-18 88-18 58 18 88 18v44h-352z"
          ></path>
        </defs>
        <g className="parallax">
          <use x="48" fill="rgba(0,0,100,0.5)" xlinkHref="#gentle-wave"></use>
          <use
            x="48"
            y="3"
            fill="rgba(0,200,255,0.4)"
            xlinkHref="#gentle-wave"
          ></use>
          <use
            x="48"
            y="5"
            fill="rgba(200,255,255,0.3)"
            xlinkHref="#gentle-wave"
          ></use>
          <use x="48" y="7" fill="#292D61" xlinkHref="#gentle-wave"></use>
        </g>
      </svg>
      <footer className="footer">
        <Container>
          <Row>
            <Col className="" md="7">
              <Row>
                <Col md="7">
                  <img alt="..." className="img-fluid center" src={footerImg} />
                </Col>

                <Col md="5" className="px-4">
                  <Row>
                    <hr
                      className="line-warning"
                      style={{ width: "100%", marginRight: "0" }}
                    />
                  </Row>

                  <Row className="d-flex justify-content-center">
                    <h3>Follow us at</h3>
                  </Row>
                  <Row className="d-flex justify-content-center">
                    <Button className="btn-icon btn-round" color="facebook">
                      <FontAwesomeIcon
                        icon={["fab", "facebook"]}
                        color="white"
                        size="lg"
                      />
                    </Button>

                    <Button className="btn-icon btn-round" color="dribbble">
                      <FontAwesomeIcon
                        icon={["fab", "instagram"]}
                        color="white"
                        size="lg"
                      />
                    </Button>

                    <Button className="btn-icon btn-round" color="linkedin">
                      <FontAwesomeIcon
                        icon={["fab", "linkedin"]}
                        color="white"
                        size="lg"
                      />
                    </Button>
                  </Row>
                </Col>
              </Row>
              <Row className="text-center w-100 mt-3">
                <h3 className="w-100">
                  All Content Reserved by SKYLAND INFRASTRUCTURES
                </h3>
                <h5 className="w-100">
                  Developed and Maintained by : Lavish Kumar 👑
                </h5>
              </Row>
            </Col>
            <Col className="" md="5">
              <div className="px-md-5">
                <hr
                  className="line-success"
                  style={{ width: "100%", marginLeft: "0" }}
                />
                <h3>Reach us at</h3>
                <ul className="list-unstyled mt-5">
                  <li className="py-2">
                    <div className="d-flex align-items-center">
                      <div className="icon icon-success mb-2">
                        <FontAwesomeIcon
                          icon={["fas", "phone-alt"]}
                          color="white"
                          size="lg"
                        />
                      </div>
                      <div className="ml-3">
                        <h6>+91-78918-87989</h6>
                      </div>
                    </div>
                  </li>
                  <li className="py-2">
                    <div className="d-flex align-items-center">
                      <div className="icon icon-success mb-2">
                        <FontAwesomeIcon
                          icon={["fas", "envelope"]}
                          color="white"
                          size="lg"
                        />
                      </div>
                      <div className="ml-3">
                        <h6>SkylandInfra@Outlook.com</h6>
                      </div>
                    </div>
                  </li>
                  <li className="py-2">
                    <div className="d-flex align-items-center">
                      <div className="icon icon-success mb-2">
                        <FontAwesomeIcon
                          icon={["fas", "map-marked-alt"]}
                          color="white"
                          size="lg"
                        />
                      </div>
                      <div className="ml-3">
                        <h6>
                          {" "}
                          C 161, Ahilya Marg, Hanuman Nagar, Jaipur, RJ (302021)
                        </h6>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </Col>
          </Row>
        </Container>
      </footer>
    </div>
  )
}
