import React from "react"
import { Link } from "gatsby"
import Typed from "../components/Typed/Typed"
import Logo from "../components/Loader/Loader"

import headerImage from "../images/projects.svg"
import ecology from "../images/contactsources.svg"
import ecoanalysis from "../images/ecoanalysis.svg"
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardTitle,
  ListGroupItem,
  ListGroup,
  Container,
  Row,
  Button,
  Col,
  CardSubtitle,
  CardText,
  CardLink,
} from "reactstrap"
import AlternateHeader from "../components/AlternateHeader/ProjectHeader"
import Construction from "../images/Construction.svg"
import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
import InfoGraphic from "../components/InfoGraphics/InfoGraphics"
import Carousel from "../components/Carousel/Carousel"
import Loader from "../components/Loader/Loader"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

class Index extends React.Component {
  componentDidMount() {
    document.body.classList.add("index-page")
  }
  componentWillUnmount() {
    document.body.classList.toggle("index-page")
  }

  render() {
    return (
      <>
 <Layout>
          <SEO title="Home" />
          <AlternateHeader>
          <div className="section h-100">
                <Row className="m-1 justify-content-between align-items-center text-center h-100">
                <div
                    className="col-md-6 order-sm-1 order-2 fakeScreen"
                    style={{ marginTop: "5rem", background: "rgba(0,0,0,0.6)" }}
                  >
                    <Row className="row p-3 justify-content-center">
                    <h3 className="m-0 bannerText1">
                      <strong className=" text-warning">Project</strong>{" "}
                      <strong className=" text-white">Portfolio</strong>
                    </h3>
                    </Row>
                  </div>

                  <div
                    className="col-md-6 order-1 p-1"
                    style={{ marginTop: "3rem" }}
                  >
                      <div className="logo">
                      <div className="logo-image">
                        <img class-name="img-fluid" src={headerImage} alt="" />
                      </div>
                    </div>
                  </div>
                </Row>
                <div>
                <div className="arrow bounce">
                    <a onClick={this.scrollToSkills} href="JavaScript:void(0)">
                      {" "}
                      <FontAwesomeIcon
                        icon={["fas", "arrow-down"]}
                        color="white"
                        size="2x"
                      />
                    </a>
                  </div>
                </div>
              </div>

     
          </AlternateHeader>

          <section className="section section-lg section-safe">
            <Container>
              <Row className="align-items-center text-left">
                <Col sm="12" lg="6">
                  <h1 className="title">
                    <strong className="text-success">Reach</strong>{" "}
                    <strong className="text-white">us at</strong>
                  </h1>
                  <p className="text-white description">
                    <Button className="btn-icon btn-round" color="success">
                      <FontAwesomeIcon
                        icon={["fas", "phone-alt"]}
                        color="white"
                        size="lg"
                      />
                    </Button>{" "}
                    +91-78918-87989
                    <br />
                    <Button className="btn-icon btn-round" color="success">
                      <FontAwesomeIcon
                        icon={["fas", "envelope"]}
                        color="white"
                        size="lg"
                      />
                    </Button>{" "}
                    SkylandInfra@Outlook.com
                    <br />
                    <Button className="btn-icon btn-round" color="success">
                      <FontAwesomeIcon
                        icon={["fas", "map-marked-alt"]}
                        color="white"
                        size="lg"
                      />
                      <br />
                    </Button>{" "}
                    C 161, Ahilya Marg, Hanuman Nagar, Jaipur, RJ (302021)
                  </p>
                </Col>
                <Col sm="12" lg="6">
                  <img
                    alt="..."
                    className="path img-fluid"
                    src={require("../assets/img/path3.png")}
                  />
                  <img src={ecology} />
                </Col>
              </Row>
            </Container>
          </section>

          <section className="section section-lg section-safe"></section>
        </Layout>
      </>
    )
  }
}

export default Index
